*{
  margin: 0;
  padding: 0;
}
section{
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  font-style: italic;
background:url(images.jpg);

}
.app-input{
  text-align: center;
  color: blueviolet;
}
h1{
  color: greenyellow;
}
.app-input input{
  font-size: 14px;
  border-radius: 5px;
  border: 1px solid gray;
  outline: none;
  padding: 10px 5px;
  width: 200px;
}
.app-input button{
  background-color: brown;
  color: whitesmoke;
  border: greenyellow;
  outline: navy;
  margin-top: 10px;
  padding: 10px 30px;
  font-size: 16px;
  cursor: pointer;
}
nav{
  background-color: violet;
  padding: 10px 0;
  display: flex;
  justify-content: center;
  margin-top: 5px;
}
nav ul{
  display: flex;
  list-style-type: none;
  justify-content: space-between;
  gap: 3px;
 align-items: center;
  width: 40%;
  color: white;
  font-weight: bolder;
}
nav ul li{
  cursor: pointer;
  font-size: 14px;
  transition: all 0.5s ease-in-out;
}
nav ul li:hover{
  transform: scale(1.0);
}
.navActive{
  color: mediumseagreen;
  font-size: 30px;
  font-weight: bold;
}
.app-container{
  background-color: khaki;
  font-size: 30px;
  color: black;
}